import React, { Component } from "react";
import { Route } from "react-router-dom";
import VendorLogin from "../Pages/Vendor/Login/Login";
import VendorHome from "../Pages/Vendor/Home/Home";
import UpcomingReservations from "../Pages/Vendor/UpcomingReservations/UpcomingReservations";
import PreviousReservations from "../Pages/Vendor/PreviousReservations/PreviousReservations";
import Reviews from "../Pages/Vendor/Reviews/Reviews";
import Menu from "../Pages/Vendor/Menu/Menu";
import EditProfile from "../Pages/Vendor/EditProfile/EditProfile";
import Registration from "../Pages/Vendor/Registration/Registration";
import { connect } from "react-redux";
import Logo from "../Images/logo.mp4";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../Config/VendorMapping";
import Firebase from "../Firebase/Firebase";
import { getVendor, setVendor } from "../Config/localStorage";
import { Spinner } from "react-bootstrap";
import AccountUnverified from "../Pages/Vendor/AccountUnverified/AccountUnverified";
import RegistrationSocial from "../Pages/Vendor/RegistrationSocial/RegistrationSocial";
class VendorRouter extends Component {
  state = { loading: true };
  componentDidMount = async () => {
    const vendorInitial = getVendor();
    console.log(vendorInitial);
    if (vendorInitial) {
      let vendor = await Firebase.getVendor(vendorInitial.uid);
      if (vendor.is_verified !== vendorInitial.is_verified) {
        setVendor(vendor);
        window.location.reload();
      } else {
        if (vendor) {
          const dishes = await Firebase.getDishesVendor(vendor.uid);
          this.props.updateDishes(dishes);
          const reviews = await Firebase.getReviews(vendor.uid);
          this.props.updateReviews(reviews);
          const categories = await Firebase.getCategories(vendor.uid);
          this.props.updateCategories(categories);
          console.log(categories);
          const customers = await Firebase.getCustomers();
          this.props.updateCustomers(customers);
          const notifications = await Firebase.getNotificationsVendor(
            vendor.uid
          );
          this.props.updateNotifications(notifications);
          const reservations = await Firebase.getReservationsVendor(vendor.uid);
          this.props.updateReservations(reservations);

          Firebase.vendorListenToDishes(snapshot => {
            const updatedValue = snapshot.val();
            let newDishes = [...this.props.dishes];
            const foundIndex = newDishes.findIndex(
              e => e.dish_key == updatedValue.dish_key
            );
            if (foundIndex >= 0) {
              newDishes[foundIndex] = updatedValue;
            } else {
              if (updatedValue.uid === vendor.uid) {
                newDishes.push(updatedValue);
              }
            }
            this.props.updateDishes(newDishes);
          });
          Firebase.vendorListenToCategories(snapshot => {
            const updatedValue = snapshot.val();
            console.log(updatedValue);
            let newCategories = [...this.props.categories];
            const foundIndex = newCategories.findIndex(
              e => e.category_key == updatedValue.category_key
            );
            if (foundIndex >= 0) {
              newCategories[foundIndex] = updatedValue;
            } else {
              newCategories.push(updatedValue);
            }
            this.props.updateCategories(newCategories);
          }, vendor.uid);
          Firebase.vendorListenToNotifications(snapshot => {
            const updatedValue = snapshot.val();
            let newNotifications = [...this.props.notifications];
            const foundIndex = newNotifications.findIndex(
              e => e.notification_key == updatedValue.notification_key
            );
            if (foundIndex >= 0) {
              newNotifications[foundIndex] = updatedValue;
            } else {
              newNotifications.push(updatedValue);
            }
            this.props.updateNotifications(newNotifications);
          }, vendor.uid);
          Firebase.vendorListenToReservations(snapshot => {
            const updatedValue = snapshot.val();
            let newReservations = [...this.props.reservations];
            const foundIndex = newReservations.findIndex(
              e => e.reservation_key == updatedValue.reservation_key
            );
            if (foundIndex >= 0) {
              newReservations[foundIndex] = updatedValue;
            } else {
              if (updatedValue.restaurant_uid === vendor.uid) {
                newReservations.push(updatedValue);
              }
            }
            this.props.updateReservations(newReservations);
          });

          this.props.updateLogin(vendor);
        }
      }
    }
    this.setState({ loading: false });
  };
  render() {
    return !this.state.loading ? (
      <>
        {this.props.vendor ? (
          <>
            {this.props.vendor.is_verified ? (
              <>
                <Route exact path="/vendor/">
                  <VendorHome></VendorHome>
                </Route>
                <Route exact path="/vendor/upcoming">
                  <UpcomingReservations></UpcomingReservations>
                </Route>
                <Route exact path="/vendor/past">
                  <PreviousReservations></PreviousReservations>
                </Route>
                <Route exact path="/vendor/reviews">
                  <Reviews></Reviews>
                </Route>
                <Route exact path="/vendor/menu">
                  <Menu></Menu>
                </Route>
                <Route exact path="/vendor/profile">
                  <EditProfile></EditProfile>
                </Route>
              </>
            ) : (
              <Route path="/vendor/">
                <AccountUnverified></AccountUnverified>
              </Route>
            )}
          </>
        ) : (
          <>
            <Route exact path="/vendor/">
              <VendorLogin></VendorLogin>
            </Route>
            <Route exact path="/vendor/register">
              <Registration></Registration>
            </Route>
            <Route exact path="/vendor/registersocial">
              <RegistrationSocial></RegistrationSocial>
            </Route>
          </>
        )}
      </>
    ) : (
      <div
        style={{ height: "100vh", background: "white" }}
        className="flex-column d-flex align-items-center justify-content-center w-100"
      >
        <video src={Logo} style={{ width: "50%" }} muted autoPlay loop></video>
        <Spinner
          style={{ width: 30, height: 30 }}
          animation="border"
          variant="dark"
        />
      </div>
    );
  }
}
export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(VendorRouter);
