import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import {
  Container,
  Row,
  Col,
  Card,
  Modal,
  Button,
  Form
} from "react-bootstrap";
import "./Menu.css";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
import Firebase from "../../../Firebase/Firebase";
class Menu extends Component {
  state = {
    addCategoryModalVisible: false,
    addDishesModalVisible: false,
    editCategoryModalVisible: false,
    editDishesModalVisible: false,
    addCategoryName: "",
    updateCategoryName: "",
    categoryToUpdate: "",
    addDishImage: null,
    addDishName: "",
    addDishCategory: "",
    addDishPrice: 0,
    addDishIngredients: "",
    updateDishImage: null,
    updateDishImageUrl: "",
    updateDishName: "",
    updateDishCategory: "",
    updateDishPrice: 0,
    updateDishIngredients: "",
    updateDishKey: ""
  };
  componentDidMount = () => {
    this.setState({
      addDishCategory: this.props.categories[0]
        ? this.props.categories[0].category
        : ""
    });
  };
  inputHandler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  setAddCategoryVisible = () => {
    this.setState({
      addCategoryModalVisible: !this.state.addCategoryModalVisible
    });
  };
  setAddDishesVisible = () => {
    this.setState({
      addDishesModalVisible: !this.state.addDishesModalVisible
    });
  };
  setEditCategoryVisible = () => {
    this.setState({
      editCategoryModalVisible: !this.state.editCategoryModalVisible
    });
  };
  setEditDishesVisible = () => {
    this.setState({
      editDishesModalVisible: !this.state.editDishesModalVisible
    });
  };
  addCategory = async () => {
    await Firebase.addCategory(
      this.state.addCategoryName,
      this.props.vendor.uid
    );
    this.setAddCategoryVisible();
    window.location.reload();
  };
  updateCategory = async () => {
    await Firebase.updateCategory(
      this.state.categoryToUpdate,
      this.state.updateCategoryName,
      this.props.vendor.uid
    );
    this.setEditCategoryVisible();
    window.location.reload();
  };
  deleteCategory = async category_key => {
    await Firebase.deleteCategory(category_key, this.props.vendor.uid);
    window.location.reload();
  };
  deleteDish = async dish_key => {
    await Firebase.deleteDish(dish_key);
    window.location.reload();
  };
  fileInputHandler = e => {
    console.log(e.target.files[0]);
    this.setState({
      [e.target.name]: e.target.files[0]
    });
  };
  dishFileInputHandler = e => {
    this.setState(
      {
        updateDishImage: e.target.files[0]
      },
      async () => {
        await Firebase.updateDishImage(
          this.state.updateDishImage,
          this.state.updateDishKey
        );
      }
    );
  };
  addDish = async () => {
    await Firebase.addDish(
      this.state.addDishName,
      this.state.addDishCategory,
      this.state.addDishIngredients,
      this.state.addDishImage,
      this.state.addDishPrice,
      this.props.vendor.uid
    );
    window.location.reload();
  };
  updateDish = async () => {
    await Firebase.updateDish(
      this.state.updateDishName,
      this.state.updateDishCategory,
      this.state.updateDishIngredients,
      this.state.updateDishPrice,
      this.state.updateDishKey
    );
    window.location.reload();
  };
  render() {
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Menu</h1>
                  </Card.Header>
                  <Card.Body>
                    <Row>
                      <Col xs="12" md="6">
                        <Card>
                          <Card.Header draggable>
                            <h4>Menu Categories</h4>
                          </Card.Header>
                          <Card.Body>
                            {this.props.categories.map(element => {
                              return (
                                <>
                                  <Row className="menu-icons d-flex justify-content-center align-items-center">
                                    <Col xs="8">{element.category}</Col>
                                    <Col
                                      xs="2"
                                      onClick={() => {
                                        this.setState({
                                          updateCategoryName: element.category,
                                          categoryToUpdate: element.category_key
                                        });
                                        this.setEditCategoryVisible();
                                      }}
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="create-outline"
                                      ></ion-icon>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() =>
                                        this.deleteCategory(
                                          element.category_key
                                        )
                                      }
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="close-circle-outline"
                                      ></ion-icon>
                                    </Col>
                                  </Row>
                                  <hr className="mb-4"></hr>
                                </>
                              );
                            })}
                            <div
                              onClick={this.setAddCategoryVisible}
                              className="menu d-flex justify-content-center align-items-center"
                            >
                              <ion-icon name="add-circle-outline"></ion-icon>
                            </div>
                          </Card.Body>
                        </Card>
                      </Col>
                      <Col xs="12" md="6">
                        <Card>
                          <Card.Header draggable>
                            <h4>Menu Dishes</h4>
                          </Card.Header>
                          <Card.Body>
                            {this.props.dishes.map(element => {
                              return (
                                <>
                                  <Row className="menu-icons d-flex justify-content-center align-items-center">
                                    <Col xs="8">
                                      <div className="d-flex align-items-center w-100">
                                        <img
                                          style={{
                                            width: 25,
                                            height: 25,
                                            borderRadius: 25,
                                            objectFit: "cover"
                                          }}
                                          src={element.photo_url}
                                        ></img>
                                        <p className="ml-3 mt-3">
                                          {element.name}
                                        </p>
                                      </div>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() => {
                                        this.setState({
                                          updateDishCategory: element.category,
                                          updateDishPrice: element.price,
                                          updateDishImageUrl: element.photo_url,
                                          updateDishIngredients:
                                            element.ingredients,
                                          updateDishKey: element.dish_key,
                                          updateDishName: element.name
                                        });
                                        this.setEditDishesVisible();
                                      }}
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="create-outline"
                                      ></ion-icon>
                                    </Col>
                                    <Col
                                      xs="2"
                                      onClick={() =>
                                        this.deleteDish(element.dish_key)
                                      }
                                    >
                                      <ion-icon
                                        className="menu-icon"
                                        name="close-circle-outline"
                                      ></ion-icon>
                                    </Col>
                                  </Row>
                                  <hr className="mb-4"></hr>
                                </>
                              );
                            })}
                            <div
                              onClick={this.setAddDishesVisible}
                              className="menu d-flex justify-content-center align-items-center"
                            >
                              <ion-icon name="add-circle-outline"></ion-icon>
                            </div>
                          </Card.Body>
                        </Card>
                      </Col>
                    </Row>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
        <Modal
          show={this.state.addCategoryModalVisible}
          size="lg"
          onHide={this.setAddCategoryVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Add Category
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Category Name</Form.Label>
              <Form.Control
                onChange={this.inputHandler}
                name="addCategoryName"
                value={this.state.addCategoryName}
                type="text"
                placeholder="Enter Category Name"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.addCategory}>Add Category</Button>
            <Button variant="danger" onClick={this.setAddCategoryVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.editCategoryModalVisible}
          size="lg"
          onHide={this.setEditCategoryVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Update Category
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Category Name</Form.Label>
              <Form.Control
                onChange={this.inputHandler}
                name="updateCategoryName"
                value={this.state.updateCategoryName}
                type="text"
                placeholder="Enter Category Name"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.updateCategory}>Update Category</Button>
            <Button variant="danger" onClick={this.setEditCategoryVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.addDishesModalVisible}
          size="lg"
          onHide={this.setAddDishesVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Add Dish
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex w-100 flex-column align-items-center justify-content-center">
              <img
                style={{
                  width: 200,
                  objectFit: "cover",
                  height: 200,
                  borderRadius: "50%"
                }}
                src={
                  this.state.addDishImage
                    ? URL.createObjectURL(this.state.addDishImage)
                    : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
                }
              ></img>
              <Form.Group className="mt-5" controlId="formGridAddress1">
                <Form.Control
                  className="btn btn-primary"
                  name="addDishImage"
                  accept="image/*"
                  onChange={this.fileInputHandler}
                  type="file"
                />
              </Form.Group>
            </div>

            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Name</Form.Label>
              <Form.Control
                value={this.state.addDishName}
                name="addDishName"
                onChange={this.inputHandler}
                type="text"
                placeholder="Enter Dish Name"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
              <Form.Label>Category</Form.Label>
              <Form.Control
                value={this.state.addDishCategory}
                name="addDishCategory"
                onChange={this.inputHandler}
                as="select"
              >
                {this.props.categories.map((cat, index) => (
                  <option selected={index === 0} value={cat.category}>
                    {cat.category}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Price</Form.Label>
              <Form.Control
                value={this.state.addDishPrice}
                name="addDishPrice"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Dish Price"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
              <Form.Label>Ingredients</Form.Label>
              <Form.Control
                placeholder="Enter Ingredients"
                as="textarea"
                value={this.state.addDishIngredients}
                name="addDishIngredients"
                onChange={this.inputHandler}
                rows="3"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.addDish}>Add Dishes</Button>
            <Button variant="danger" onClick={this.setAddDishesVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
        <Modal
          show={this.state.editDishesModalVisible}
          size="lg"
          onHide={this.setEditDishesVisible}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              Edit Dish
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="d-flex w-100 flex-column align-items-center justify-content-center">
              <img
                style={{
                  width: 200,
                  objectFit: "cover",
                  height: 200,
                  borderRadius: "50%"
                }}
                src={
                  this.state.updateDishImage
                    ? URL.createObjectURL(this.state.updateDishImage)
                    : this.state.updateDishImageUrl
                }
              ></img>
              <Form.Group className="mt-5" controlId="formGridAddress1">
                <Form.Control
                  className="btn btn-primary"
                  name="updateDishImage"
                  accept="image/*"
                  onChange={this.dishFileInputHandler}
                  type="file"
                />
              </Form.Group>
            </div>

            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Name</Form.Label>
              <Form.Control
                value={this.state.updateDishName}
                name="updateDishName"
                onChange={this.inputHandler}
                type="text"
                placeholder="Enter Dish Name"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
              <Form.Label>Category</Form.Label>
              <Form.Control
                value={this.state.updateDishCategory}
                name="updateDishCategory"
                onChange={this.inputHandler}
                as="select"
              >
                {this.props.categories.map((cat, index) => (
                  <option
                    selected={this.state.updateDishCategory === cat.category}
                    value={cat.category}
                  >
                    {cat.category}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlInput1">
              <Form.Label>Dish Price</Form.Label>
              <Form.Control
                value={this.state.updateDishPrice}
                name="updateDishPrice"
                onChange={this.inputHandler}
                type="number"
                placeholder="Enter Dish Price"
              />
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
              <Form.Label>Ingredients</Form.Label>
              <Form.Control
                placeholder="Enter Ingredients"
                as="textarea"
                value={this.state.updateDishIngredients}
                name="updateDishIngredients"
                onChange={this.inputHandler}
                rows="3"
              />
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.updateDish}>Edit Dishes</Button>
            <Button variant="danger" onClick={this.setEditDishesVisible}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default connect(vendorMapStateToProps, vendorMapDispatchToProps)(Menu);
