import { VendorActionTypes } from "../Actions/ActionTypes";

const initialState = {
  vendor: null,
  reservations: [],
  reviews: [],
  customers: [],
  categories: [],
  dishes: [],
  notifications: [],
  registrationInfo: {
    name: "",
    email: "",
    uid: "",
    photoUrl: ""
  }
};

const VendorReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case VendorActionTypes.UPDATE_CATEGORIES: {
      return { ...state, categories: payload };
    }
    case VendorActionTypes.UPDATE_CUSTOMERS: {
      return { ...state, customers: payload };
    }
    case VendorActionTypes.UPDATE_DISHES: {
      return { ...state, dishes: payload };
    }
    case VendorActionTypes.UPDATE_NOTIFICATIONS: {
      return { ...state, notifications: payload };
    }
    case VendorActionTypes.UPDATE_RESERVATIONS: {
      return { ...state, reservations: payload };
    }
    case VendorActionTypes.UPDATE_REVIEWS: {
      return { ...state, reviews: payload };
    }
    case VendorActionTypes.VENDOR_UPDATE_LOGIN_STATUS: {
      return { ...state, vendor: payload };
    }
    case VendorActionTypes.UPDATE_REGISTRATION_SOCIAL: {
      return { ...state, registrationInfo: payload };
    }
    default:
      return state;
  }
};
export default VendorReducer;
