import { VendorActionTypes } from "../Actions/ActionTypes";

const vendorMapStateToProps = state => {
  return state.VendorReducer;
};
const vendorMapDispatchToProps = dispatch => {
  return {
    updateLogin: user =>
      dispatch({
        type: VendorActionTypes.VENDOR_UPDATE_LOGIN_STATUS,
        payload: user
      }),
    updateNotifications: notifications =>
      dispatch({
        type: VendorActionTypes.UPDATE_NOTIFICATIONS,
        payload: notifications
      }),
    updateReservations: reservations =>
      dispatch({
        type: VendorActionTypes.UPDATE_RESERVATIONS,
        payload: reservations
      }),
    updateReviews: reviews =>
      dispatch({ type: VendorActionTypes.UPDATE_REVIEWS, payload: reviews }),
    updateCustomers: customers =>
      dispatch({
        type: VendorActionTypes.UPDATE_CUSTOMERS,
        payload: customers
      }),
    updateCategories: categories =>
      dispatch({
        type: VendorActionTypes.UPDATE_CATEGORIES,
        payload: categories
      }),
    updateDishes: dishes =>
      dispatch({ type: VendorActionTypes.UPDATE_DISHES, payload: dishes }),
    updateRegistrationSocial: socialInfo =>
      dispatch({
        type: VendorActionTypes.UPDATE_REGISTRATION_SOCIAL,
        payload: socialInfo
      })
  };
};
export { vendorMapDispatchToProps, vendorMapStateToProps };
