import React, { Component } from "react";
import Logo from "../../../Images/logo.mp4";
export default class AccountUnverified extends Component {
  render() {
    return (
      <div
        style={{ height: "100vh", background: "white" }}
        className="flex-column d-flex align-items-center justify-content-center w-100"
      >
        <video src={Logo} style={{ width: "50%" }} muted autoPlay></video>
        <p className="text-muted">
          Please wait while your account is being verified by our
          administrators.
        </p>
      </div>
    );
  }
}
