import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import {
  Card,
  Container,
  Row,
  Col,
  Form,
  InputGroup,
  FormControl,
  Button,
  Spinner
} from "react-bootstrap";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
import Firebase from "../../../Firebase/Firebase";
import { setVendor } from "../../../Config/localStorage";
import { withRouter } from "react-router-dom";
class EditProfile extends Component {
  state = {
    phone: "",
    reservationFee: 0,
    location: {
      lat: 0,
      long: 0
    },
    bankName: "",
    accountName: "",
    accountNumber: "",
    address: "",
    imageUploading: false,
    vendorUpdating: false,
    name: ""
  };
  componentDidMount = () => {
    this.setState({
      phone: this.props.vendor.number,
      reservationFee: this.props.vendor.reservation_fee,
      location: {
        lat: this.props.vendor.llocation.lat,
        long: this.props.vendor.llocation.llong
      },
      bankName: this.props.vendor.bank_details.bank_name,
      accountName: this.props.vendor.bank_details.account_name,
      accountNumber: this.props.vendor.bank_details.accountNumber,
      address: this.props.vendor.address,
      name: this.props.vendor.name
    });
  };
  updateImage = async e => {
    this.setState({
      imageUploading: true
    });
    await Firebase.updateVendorImage(e.target.files[0], this.props.vendor.uid);
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
    this.setState({
      imageUploading: false
    });
  };
  inputHandler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  submit = async () => {
    this.setState({
      imageUploading: true
    });
    await Firebase.updateVendor(
      this.state.name,
      this.state.phone,
      this.state.reservationFee,
      this.state.location.lat,
      this.state.location.long,
      this.state.accountNumber,
      this.state.accountName,
      this.state.bankName,
      this.props.vendor.uid
    );
    const vendor = await Firebase.getVendor(this.props.vendor.uid);
    this.props.updateLogin(vendor);
    setVendor(vendor);
    this.setState({
      imageUploading: false
    });
    this.props.history.push("/vendor/");
  };
  render() {
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Profile Setting</h1>
                  </Card.Header>
                  <Card.Body>
                    <div className="d-flex flex-column justify-content-center align-items-center">
                      <img
                        style={{
                          width: "200px",
                          height: "200px",
                          objectFit: "cover",
                          borderRadius: "50%"
                        }}
                        src={
                          this.props.vendor.photo_url
                            ? this.props.vendor.photo_url
                            : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
                        }
                      ></img>
                      {this.state.imageUploading ? (
                        <Spinner
                          style={{ width: 30, height: 30 }}
                          animation="border"
                          variant="dark"
                          className="mt-5"
                        />
                      ) : (
                        <Form.Group
                          className="mt-5"
                          controlId="formGridAddress1"
                        >
                          <Form.Control
                            className="btn btn-primary"
                            accept="image/*"
                            onChange={this.updateImage}
                            type="file"
                          />
                        </Form.Group>
                      )}
                    </div>
                    <hr className="my-5"></hr>
                    <Form.Group controlId="formGroupEmail">
                      <Form.Label>Vendor Name</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter Vendor Name"
                        value={this.state.name}
                        name="name"
                        onChange={this.inputHandler}
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                      <Form.Label>Vendor Type</Form.Label>
                      <Form.Control disabled as="select">
                        <option
                          selected={
                            this.props.vendor.category === "Restaurants"
                          }
                          value="Restaurant"
                        >
                          Restaurant
                        </option>
                        <option
                          selected={this.props.vendor.category === "Cafes"}
                          value="Cafe"
                        >
                          Cafe
                        </option>
                        <option
                          selected={
                            this.props.vendor.category === "Escape Rooms"
                          }
                          value="Escape Room"
                        >
                          Escape Room
                        </option>
                        <option
                          selected={this.props.vendor.category === "Events"}
                          value="Event"
                        >
                          Event
                        </option>
                      </Form.Control>
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlSelect2">
                      <Form.Label>Branch</Form.Label>
                      <Form.Control disabled as="select">
                        <option
                          selected={this.props.vendor.branch === "Main"}
                          value="Main"
                        >
                          Main
                        </option>
                        <option
                          selected={this.props.vendor.branch === "Sub"}
                          value="Sub"
                        >
                          Sub
                        </option>
                      </Form.Control>
                    </Form.Group>
                    <hr className="my-5"></hr>
                    <Form.Group controlId="exampleForm.ControlInput1">
                      <Form.Label>Email address</Form.Label>
                      <Form.Control
                        value={this.props.vendor.email}
                        disabled
                        type="email"
                        placeholder="Enter your Email Address"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput2">
                      <Form.Label>Phone Number</Form.Label>
                      <Form.Control
                        value={this.state.phone}
                        name="phone"
                        onChange={this.inputHandler}
                        type="text"
                        placeholder="Enter your Phone"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Reservation Fee</Form.Label>
                      <InputGroup className="mb-3">
                        <InputGroup.Prepend>
                          <InputGroup.Text>EGP</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                          value={this.state.reservationFee}
                          name="reservationFee"
                          onChange={this.inputHandler}
                          type="number"
                          aria-label="Amount (to the nearest dollar)"
                        />
                      </InputGroup>
                    </Form.Group>
                    <hr className="my-5"></hr>
                    <h3>Location</h3>
                    <hr className="my-5"></hr>
                    <h3 className="mb-5">Bank Details</h3>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Bank Name</Form.Label>
                      <Form.Control
                        value={this.state.bankName}
                        name="bankName"
                        onChange={this.inputHandler}
                        type="text"
                        placeholder="Enter your Bank Name"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Account Name</Form.Label>
                      <Form.Control
                        value={this.state.accountName}
                        name="accountName"
                        onChange={this.inputHandler}
                        type="text"
                        placeholder="Enter your Account Name"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Account Number</Form.Label>
                      <Form.Control
                        value={this.state.accountNumber}
                        name="phone"
                        onChange={this.inputHandler}
                        type="number"
                        placeholder="Enter your Account Number"
                      />
                    </Form.Group>
                    <hr className="my-5"></hr>
                    {this.state.vendorUpdating ? (
                      <Spinner
                        style={{ width: 30, height: 30 }}
                        animation="border"
                        variant="dark"
                        className="mt-5"
                      />
                    ) : (
                      <Button
                        onClick={this.submit}
                        variant="primary"
                        size="lg"
                        block
                      >
                        Save
                      </Button>
                    )}
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
      </div>
    );
  }
}
export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(withRouter(EditProfile));
