const setAdmin = user => {
  localStorage.setItem("admin", JSON.stringify(user));
};
const getAdmin = () => {
  const user = JSON.parse(localStorage.getItem("admin"));
  return user;
};
const removeAdmin = () => {
  localStorage.removeItem("admin");
};
const setVendor = user => {
  localStorage.setItem("Vendor", JSON.stringify(user));
};
const getVendor = () => {
  const user = JSON.parse(localStorage.getItem("Vendor"));
  return user;
};
const removeVendor = () => {
  localStorage.removeItem("Vendor");
};
export { setAdmin, getAdmin, removeAdmin, setVendor, getVendor, removeVendor };
