import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import {
  Container,
  Row,
  Col,
  Card,
  Table,
  Accordion,
  Button
} from "react-bootstrap";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";

class UpcomingReservations extends Component {
  render() {
    const date = new Date();
    const yesterday = new Date().setDate(date.getDate() - 1);
    const reservations = this.props.reservations.filter(element => {
      const elementDate = new Date(element.date);
      return elementDate > yesterday;
    });

    const reservationsToRender = {};
    reservations.forEach(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      if (reservationsToRender[element.date]) {
        reservationsToRender[element.date].arr.push({ ...element, customer });
      } else {
        const arr = [{ customer, ...element }];
        reservationsToRender[element.date] = { arr };
      }
    });
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Upcoming Reservations</h1>
                  </Card.Header>
                  <Card.Body>
                    <Accordion>
                      {Object.keys(reservationsToRender).map((key, index) => {
                        const reserve = reservationsToRender[key];
                        return (
                          <Card>
                            <Card.Header>
                              <Accordion.Toggle
                                as={Button}
                                eventKey={"" + index}
                              >
                                {key} Upcoming Reservations
                              </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey={"" + index}>
                              <Card.Body>
                                <Table striped hover responsive>
                                  <thead>
                                    <tr>
                                      <th>Customer Image</th>
                                      <th colSpan="4">Description</th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {reserve.arr.map(element => {
                                      return (
                                        <tr>
                                          <td>
                                            <img
                                              style={{
                                                width: 50,
                                                height: 50,
                                                borderRadius: 50,
                                                objectFit: "cover"
                                              }}
                                              src={
                                                element.customer.photo_url
                                                  ? element.customer.photo_url
                                                  : "https://www.rd.com/wp-content/uploads/2017/09/01-shutterstock_476340928-Irina-Bg-1024x683.jpg"
                                              }
                                            ></img>
                                          </td>
                                          <td colSpan="4">
                                            <strong>Customer Name: </strong>{" "}
                                            {element.customer.name}
                                            <br></br>
                                            <strong>
                                              Reservation Time:{" "}
                                            </strong>{" "}
                                            {element.time}
                                            <br></br>
                                            <strong>
                                              Number of Attendees:{" "}
                                            </strong>{" "}
                                            {element.attandee}
                                            <br></br>
                                            <strong>
                                              Number of Tables:{" "}
                                            </strong>{" "}
                                            {Math.floor(
                                              Number(element.attandee / 5)
                                            ) + 1}
                                            <br></br>
                                            <strong>
                                              Status of Reservation:{" "}
                                            </strong>{" "}
                                            {element.status}
                                            <br></br>
                                            <strong>
                                              Customer Phone number:{" "}
                                            </strong>{" "}
                                            {element.customer.number}
                                            <br></br>
                                            <strong>
                                              Customer Email:{" "}
                                            </strong>{" "}
                                            {element.customer.email}
                                            <br></br>
                                          </td>
                                        </tr>
                                      );
                                    })}
                                  </tbody>
                                </Table>
                              </Card.Body>
                            </Accordion.Collapse>
                          </Card>
                        );
                      })}
                    </Accordion>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
      </div>
    );
  }
}
export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(UpcomingReservations);
