import * as firebase from "firebase";
import firebaseConfig from "../Config";

const app = firebase.initializeApp(firebaseConfig);
const registerVendorEmail = async (email, password) => {
  const user = await app.auth().createUserWithEmailAndPassword(email, password);
  return user;
};
const createVendor = async (
  email,
  password,
  address,
  accountNumber,
  account_name,
  bank_name,
  branch,
  category,
  lat,
  llong,
  name,
  number,
  photo,
  photo_extension,
  reservation_fee
) => {
  const user = await registerVendorEmail(email, password);
  const uid = user.user.uid;
  const photo_url = await uploadVendorImage(photo, photo_extension, uid);
  await app
    .database()
    .ref("/restaurants/" + uid)
    .set({
      email,
      address,
      bank_details: { account_name, accountNumber, bank_name },
      branch,
      category,
      reservation_fee,
      photo_url,
      number,
      llocation: {
        lat,
        llong
      },
      name,
      uid,
      is_verified: false,
      is_restrict_order: false,
      is_number_verified: false,
      is_email_verified: false,
      close: false
    });
  const response = await app
    .database()
    .ref("/restaurants/" + uid)
    .once("value");
  return response.val();
};
