const uploadVendorImage = async (image, extension, uid) => {
  const storageRef = app
    .storage()
    .ref("/restaurant_images")
    .child("/" + uid + "." + extension);
  const uploadedFile = await storageRef.put(image);

  return uploadedFile.ref.getDownloadURL();
};
const uploadDishImage = async (image, extension, uid) => {
  const storageRef = app
    .storage()
    .ref("/dish_images")
    .child("/" + uid + "." + extension);
  const uploadedFile = await storageRef.put(image);
  return uploadedFile.ref.getDownloadURL();
};
const addCategory = async (category, uid) => {
  const category_key = (
    await app
      .database()
      .ref("/categories/" + uid)
      .push()
  ).key;
  await app
    .database()
    .ref("/categories/" + uid + "/" + category_key)
    .set({
      category,
      category_key
    });
};
const addDish = async (name, category, ingredients, image, price, uid) => {
  const dish_key = (
    await app
      .database()
      .ref("/dishes/")
      .push()
  ).key;
  const imageSplit = image.name.split(".");
  const photo_url = await uploadDishImage(
    image,
    imageSplit[imageSplit.length - 1],
    dish_key
  );
  await app
    .database()
    .ref("/dishes/" + dish_key)
    .set({
      category,
      dish_key,
      uid,
      ingredients,
      name,
      price,
      photo_url
    });
};
