const addRoom = async (
  uid,
  name,
  room_description,
  image,
  total_participants
) => {
  const room_key = (
    await app
      .database()
      .ref("/restaurants/" + uid + "/instant_data/")
      .push()
  ).key;
  const extension = image.name.split(".")[image.name.split(".").length - 1];
  const room_image_url = await uploadRoomImage(image, extension, room_key);
  await app
    .database()
    .ref("/restaurants/" + uid + "/instant_data/" + room_key)
    .set({
      name,
      room_description,
      room_image_url,
      room_key,
      total_participants,
      time_slots: {}
    });
};

const addRoomTimeSlot = async (uid, room_key, time_slot) => {
  const time_slot_key = (
    await app
      .database()
      .ref("/restaurants/" + uid + "/instant_data/" + room_key + "/time_slots/")
      .push()
  ).key;
  await app
    .database()
    .ref(
      "/restaurants/" +
        uid +
        "/instant_data/" +
        room_key +
        "/time_slots/" +
        time_slot_key
    )
    .set({
      time_slot,
      time_slot_key,
      hide: false
    });
};
