const getReservationsVendor = async uid => {
  const response = await app
    .database()
    .ref("/reservations/")
    .once("value");
  const responseValue = response.val();
  const reservations = responseValue
    ? Object.keys(responseValue).map(key => {
        return responseValue[key];
      })
    : [];

  const filteredReservations = reservations.filter(
    e => e.restaurant_uid === uid
  );
  return filteredReservations;
};
