const getNotifications = async uid => {
  const response = await app
    .database()
    .ref("/notifications/" + uid)
    .once("value");
  const value = response.val();
  if (value) {
    const notifications = Object.keys(value).map(key => {
      return value[key];
    });
    return notifications;
  }
  return [];
};
const getAllReviews = async () => {
  const response = await app
    .database()
    .ref("/ratings/")
    .once("value");
  const value = response.val();
  const ratings = [];
  Object.keys(value).forEach(key => {
    const data = Object.keys(value[key]).map(k => value[key][k]);
    ratings.push(...data);
  });
  return ratings;
};
