const addDiscountVoucher = async (
  code,
  expDate,
  maxUses,
  vendor_type,
  percentage
) => {
  const key = await app
    .database()
    .ref("/discounts")
    .push();
  await app
    .database()
    .ref("/discounts/" + key.key)
    .set({
      code,
      expDate,
      maxUses,
      numberOfUses: 0,
      isBlocked: false,
      uid: key.key,
      vendor_type,
      percentage
    });
};
