const addSlideShow = async (file, extension, vendor_uid) => {
  const key = (
    await app
      .database()
      .ref("/slideshows/")
      .push()
  ).key;
  const image_url = await uploadSlideShowImage(file, key, extension);
  await app
    .database()
    .ref("/slideshows/" + key)
    .set({
      uid: key,
      vendor_uid,
      image_url
    });
};
const uploadSlideShowImage = async (file, uid, extension) => {
  const ref = app.storage().ref("/slideshows/");
  var imageRef = ref.child(uid + "." + extension);
  const response = await imageRef.put(file);
  return response.ref.getDownloadURL();
};
