import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  InputGroup,
  Button,
  FormControl
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import Firebase from "../../../Firebase/Firebase";
import { connect } from "react-redux";
import {
  vendorMapDispatchToProps,
  vendorMapStateToProps
} from "../../../Config/VendorMapping";
import { setVendor } from "../../../Config/localStorage";
class Registration extends Component {
  state = {
    image: null,
    name: "",
    vendorType: "Restaurants",
    branch: "Main",
    email: "",
    phone: "",
    reservationFee: 0,
    location: {
      lat: 0,
      long: 0
    },
    bankName: "",
    accountName: "",
    accountNumber: "",
    password: "",
    confirmPassword: "",
    address: ""
  };
  inputHandler = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  fileInputHandler = e => {
    console.log(e.target.files[0]);
    this.setState({
      [e.target.name]: e.target.files[0]
    });
  };
  register = async () => {
    const user = await Firebase.createVendor(
      this.state.email,
      this.state.password,
      this.state.address,
      this.state.accountNumber,
      this.state.accountName,
      this.state.bankName,
      this.state.branch,
      this.state.vendorType,
      this.state.location.lat,
      this.state.location.long,
      this.state.name,
      this.state.phone,
      this.state.image,
      this.state.image.name.split(".")[
        this.state.image.name.split(".").length - 1
      ],
      this.state.reservationFee
    );
    if (user) {
      alert("Account has been registered");
      const vendor = await Firebase.loginVendor(
        this.state.email,
        this.state.password
      );
      this.props.updateLogin(vendor);
      setVendor(vendor);
      this.props.history.push("/vendor/");
      window.location.reload();
    } else {
      alert("Account could not be registered");
    }
  };
  render() {
    return (
      <div>
        <Container fluid>
          <Row>
            <Col
              xs="12"
              style={{
                padding: 50,
                paddingTop: "10vh",
                paddingBottom: "10vh",
                backgroundColor: "#ba0fb4"
              }}
            >
              <Card>
                <Card.Header>
                  <h1>Vendor Registration</h1>
                </Card.Header>
                <Card.Body>
                  <div className="d-flex flex-column justify-content-center align-items-center">
                    <img
                      style={{
                        width: "200px",
                        height: "200px",
                        objectFit: "cover",
                        borderRadius: "50%"
                      }}
                      src={
                        this.state.image
                          ? URL.createObjectURL(this.state.image)
                          : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
                      }
                    ></img>
                    <Form.Group className="mt-5" controlId="formGridAddress1">
                      <Form.Control
                        className="btn btn-primary"
                        name="image"
                        accept="image/*"
                        onChange={this.fileInputHandler}
                        type="file"
                      />
                    </Form.Group>
                  </div>
                  <hr className="my-5"></hr>
                  <Form.Group controlId="formGroupEmail">
                    <Form.Label>Vendor Name</Form.Label>
                    <Form.Control
                      name="name"
                      onChange={this.inputHandler}
                      type="text"
                      placeholder="Enter Vendor Name"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label>Vendor Type</Form.Label>
                    <Form.Control
                      name="vendorType"
                      onChange={this.inputHandler}
                      as="select"
                    >
                      <option value="Restaurants">Restaurant</option>
                      <option value="Cafes">Cafe</option>
                      <option value="Escape Rooms">Escape Room</option>
                      <option value="Events">Event</option>
                    </Form.Control>
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlSelect2">
                    <Form.Label>Branch</Form.Label>
                    <Form.Control
                      name="branch"
                      onChange={this.inputHandler}
                      as="select"
                    >
                      <option value="Main">Main</option>
                      <option value="Sub">Sub</option>
                    </Form.Control>
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control
                      name="email"
                      onChange={this.inputHandler}
                      type="email"
                      placeholder="Enter your Email Address"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Address</Form.Label>
                    <Form.Control
                      name="address"
                      onChange={this.inputHandler}
                      type="text"
                      placeholder="Enter your Address"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput2">
                    <Form.Label>Phone Number</Form.Label>
                    <Form.Control
                      name="phone"
                      onChange={this.inputHandler}
                      type="text"
                      placeholder="Enter your Phone"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput3">
                    <Form.Label>Reservation Fee</Form.Label>
                    <InputGroup className="mb-3">
                      <InputGroup.Prepend>
                        <InputGroup.Text>EGP</InputGroup.Text>
                      </InputGroup.Prepend>
                      <FormControl
                        name="reservationFee"
                        onChange={this.inputHandler}
                        type="number"
                        aria-label="Amount (to the nearest dollar)"
                      />
                    </InputGroup>
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <p>Location</p>
                  <hr className="my-5"></hr>
                  <h3 className="mb-5">Bank Details</h3>
                  <Form.Group controlId="exampleForm.ControlInput3">
                    <Form.Label>Bank Name</Form.Label>
                    <Form.Control
                      name="bankName"
                      onChange={this.inputHandler}
                      type="text"
                      placeholder="Enter your Bank Name"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput7">
                    <Form.Label>Account Name</Form.Label>
                    <Form.Control
                      name="accountName"
                      onChange={this.inputHandler}
                      type="text"
                      placeholder="Enter your Account Name"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput6">
                    <Form.Label>Account Number</Form.Label>
                    <Form.Control
                      name="accountNumber"
                      onChange={this.inputHandler}
                      type="number"
                      placeholder="Enter your Account Number"
                    />
                  </Form.Group>
                  <hr className="my-5"></hr>

                  <h3 className="mb-5">Change Password</h3>
                  <Form.Group controlId="exampleForm.ControlInput4">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      name="password"
                      onChange={this.inputHandler}
                      type="password"
                      placeholder="Enter your Password"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput5">
                    <Form.Label>Confirm Password</Form.Label>
                    <Form.Control
                      name="confirmPassword"
                      onChange={this.inputHandler}
                      type="password"
                      placeholder="Renter your Password"
                    />
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <Button
                    onClick={this.register}
                    variant="primary"
                    size="lg"
                    block
                  >
                    Submit Registration
                  </Button>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(withRouter(Registration));
